import React, { useState, useEffect } from 'react';
import { fetch, getPostsUrl } from '../utils/dao';

// Components
import { Layout, Container } from '../components';

// Data
import data from '../data/pages/post.json';

const Post = () => {
  const [post, setPost] = useState({});

  const POST_PARAMS = {
    id:
      typeof window !== 'undefined'
        ? window.location.pathname.split('/').filter(Boolean)[1]
        : '',
  };

  const getPost = () => {
    fetch(getPostsUrl(POST_PARAMS)).then((results) => {
      let post = results.response.posts[0];
      if (post.photos && data.content)
      data.content.hero.imagePath = post.photos[0].alt_sizes[0].url;
      if (post.tags && data.content) data.content.hero.title = post.tags[0];
      setPost(post);
    });
  };

  useEffect(() => {
    getPost();
  }, []);

  return (
    <>
      {Object.keys(post).length > 0 && (
        <Layout pageContext={data}>
          <Container>
            <div dangerouslySetInnerHTML={{ __html: post.caption }} />
          </Container>
        </Layout>
      )}
    </>
  );
};

export default Post;
