const POST_URL = 'https://api.tumblr.com/v2/blog/aristowealth.tumblr.com/posts';
const API_KEY = 'a9rmrkt46nr8csi1Fkp9VarPafZws8ZQJZyitfTtDWcwB5GhHe';

export const encodeParams = (params = {}) => {
  return Object.keys(params)
  .map(key => `${key}=${params[key]}`)
  .join('&');
};

export const getPostsUrl = (params = {}, next) => {
  params.api_key = API_KEY;

  return next
      ? `https://api.tumblr.com${next}&api_key=${API_KEY}`
      : `${POST_URL}?${encodeParams(params)}`;
};

export const fetch = (endpoint = '', options = {
  method: 'GET',
  headers: {
      'Content-Type': 'application/json; charset=utf-8',
  },
}) => {
  return window.fetch(endpoint, options)
    .then((response) => {
      const {
        ok,
        status,
        statusText,
        url,
      } = response;

      if (ok) {
        return response.json();
      }

      const error = {
        message: `${status}: ${statusText}, failure for call to ${url}`,
        response,
        status,
        statusText,
      };

      return Promise.reject(error);
    })
    .catch((error) => {
      return Promise.reject(error);
    });
}